:root {
  --container: 1100px;
  --accent: #1976d2;
  --muted: #666;
  --card: #fff;
}

* { box-sizing: border-box; }
body { margin:0; font-family: Inter, system-ui, Arial, sans-serif; color:#222; background:#f7f9fc; }

.container { max-width: var(--container); margin: 0 auto; padding: 0 16px; }

.site-header { background: #fff; border-bottom: 1px solid #eaeaea; }
.header-inner { display:flex; align-items:center; justify-content:space-between; padding:12px 0; }
.brand { display:flex; gap:12px; align-items:center; }
.logo { width:56px; height:56px; object-fit:cover; border-radius:6px; }
.title h1 { margin:0; font-size:18px; }
.title small { color:var(--muted); }

.main-nav .nav-link { margin-left:12px; color:#333; text-decoration:none; padding:8px 10px; }
.main-nav .nav-link:hover { background:#f0f6ff; border-radius:6px; }

.banner-slider { position:relative; height:260px; overflow:hidden; }
.banner-slider .slide { position:absolute; inset:0; display:flex; align-items:center; justify-content:center; transition:opacity .8s ease, transform 6s ease; opacity:0; transform: scale(1); }
.banner-slider .slide.active { opacity:1; transform: scale(1.03); }
.slide-inner { text-align:center; }
.slide-inner h2 { margin: 0 0 6px; font-size:28px; }
.slide-inner p { margin:0; color:var(--muted); }

.slider-dots { position:absolute; bottom:12px; left:50%; transform:translateX(-50%); display:flex; gap:8px; }
.dot { width:10px; height:10px; border-radius:50%; background:#ddd; border:none; }
.dot.active { background:var(--accent); }

.updates-ticker { background:#fff; border-bottom:1px solid #eee; overflow:hidden; }
.ticker-track { display:flex; gap:32px; padding:8px 16px; animation: ticker 18s linear infinite; align-items:center; }
.tick { white-space:nowrap; font-size:14px; color:#333; }
@keyframes ticker {
  0% { transform: translateX(0%); }
  100% { transform: translateX(-50%); }
}

.main-wrap { padding: 24px 0; }

.upload-section { background:#fff; padding:16px; border-radius:8px; box-shadow:0 1px 3px rgba(0,0,0,.04); margin-bottom:20px; }
.upload-form .row { display:flex; gap:12px; margin-bottom:12px; flex-wrap:wrap; }
.upload-form label { flex:1 1 200px; display:flex; flex-direction:column; font-size:13px; }
.upload-form input, .upload-form select { padding:8px; border:1px solid #ddd; border-radius:6px; margin-top:6px; }
.upload-form .actions { display:flex; gap:12px; }
.btn { background:var(--accent); color:#fff; padding:8px 12px; border:none; border-radius:6px; cursor:pointer; }
.btn:hover { opacity:0.95; }

.semesters-grid { display:grid; grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); gap:14px; margin-top:14px; }
.sem-tile { display:block; text-decoration:none; color:inherit; background:#fff; border-radius:8px; padding:16px; box-shadow:0 1px 2px rgba(0,0,0,.04); align-items:center; justify-content:center; text-align:center; }
.sem-image { height:90px; background:linear-gradient(45deg,#fff,#f2f6ff); border-radius:6px; margin-bottom:8px; }
.sem-title { font-weight:600; }

.hero { height:120px; background:linear-gradient(90deg,#fff,#f9fbff); display:flex; align-items:center; border-bottom:1px solid #eee; }
.hero-inner { padding:18px 16px; }

.filter-form { background:#fff; padding:12px; border-radius:8px; box-shadow:0 1px 3px rgba(0,0,0,.04); margin-top:12px; }
.filter-form .row { display:flex; gap:12px; align-items:end; flex-wrap:wrap; }

.materials-list { display:flex; flex-direction:column; gap:10px; margin-top:12px; }
.material-card { display:flex; justify-content:space-between; gap:12px; padding:12px; border-radius:8px; background:#fff; border:1px solid #f0f0f0; align-items:center; }
.material-meta h4 { margin:0; }
.material-meta .small { color:var(--muted); font-size:13px; margin-top:6px; }
.material-actions .btn { margin-left:8px; }

.no-items { text-align:center; padding:24px; }
.no-items img { max-width:220px; opacity:.9; }

.site-footer { padding:12px 0; background:#fff; border-top:1px solid #eee; margin-top:20px; text-align:center; color:var(--muted); }

.form-msg { margin-top:8px; color:var(--accent); }

/* Responsive */
@media (max-width:800px) {
  .header-inner { flex-direction:column; gap:8px; align-items:flex-start; }
  .main-nav { display:flex; gap:6px; }
  .banner-slider { height:180px; }
  .hero { height:100px; }
}
